function hoverImage() {
    const imagenesDiv = document.querySelectorAll('.image-container');

    imagenesDiv.forEach(imageDiv => {
        const divInfo = document.createElement('DIV');
        divInfo.classList.add('div-info');
        divInfo.textContent = 'Texto de ejemplo';
        divInfo.role = "dialog"

        imageDiv.addEventListener('mouseover', () => {
            //Clase a el div entero
            // imageDiv.classList.add('big-info')

            //Agregar texto al div entero
            imageDiv.appendChild(divInfo);
            divInfo.classList.add('show'); // Agrega clase para mostrar el div
        });

        imageDiv.addEventListener('mouseout', () => {
            imageDiv.classList.remove('big-info')
            imageDiv.removeChild(divInfo);
            divInfo.classList.remove('show');
        });
    });
}

function ocultarBarraDesplazamiento() {
    var wrapper = document.querySelector('.browse-images-container');
    var container = document.querySelector('.browse-images-container li');
    var contenidoWidth = container.scrollWidth;
    var wrapperWidth = wrapper.clientWidth;

    if (contenidoWidth > wrapperWidth) {
        var scrollbarWidth = contenidoWidth - wrapperWidth;
        wrapper.style.paddingRight = scrollbarWidth + 'px';
    } else {
        wrapper.style.paddingRight = '0';
    }
}

function boton(){
    const scrollContainers = document.querySelectorAll('.browse-images-container');
    
    scrollContainers.forEach((scrollContainer) => {

        const rightButton = scrollContainer.nextElementSibling;
        const leftButton = scrollContainer.previousElementSibling;
        
        rightButton.addEventListener('click', ()=>{    
            const containerWidth = scrollContainer.offsetWidth;
            scrollContainer.scrollBy({
                left: containerWidth,
                behavior: "smooth",
            });

            if ((scrollContainer.scrollLeft + containerWidth) >= (scrollContainer.scrollWidth)) {
                scrollContainer.scrollBy({
                    left: -scrollContainer.scrollWidth,
                    behavior: "smooth",
                });
            }
        })
        
        leftButton.addEventListener('click', ()=>{    
            const containerWidth = scrollContainer.offsetWidth;
            scrollContainer.scrollBy({
                left: -containerWidth,
                behavior: "smooth",
            });

            if (scrollContainer.scrollLeft <= 0)  {
                console.log('scrollContainer.scrollLeft')

                scrollContainer.scrollBy({
                    left: scrollContainer.scrollWidth,
                    behavior: "smooth",
                });
            }

        })
    })   
}

function fixedNav(){
    const nav = document.querySelector('.browse-nav');

    window.addEventListener('scroll', ()=>{
        const height = nav.scrollHeight;
        const scrollPosition = window.scrollY;

        if (height > 157) {
            nav.classList.remove('fixed');

        } else if (scrollPosition > height) {
            nav.classList.add('fixed');
            
        } else{
            nav.classList.remove('fixed');

        }

    })
}

function animations(){
    //Notification animation
    const notification = document.querySelector('.notification-icon');
    // animation.classList.remove('animate__animated', 'animate__fadeIn');

    notification.classList.remove('animate__animated', 'animate__fadeIn');
    setTimeout(() => {
        notification.classList.add('animate__animated', 'animate__fadeIn');
    }, 100);
}

function showMoreInfo(){
    const images = document.querySelectorAll('.image-container img');
    images.forEach(image =>{
        image.addEventListener('click', ()=>{
            const body = document.body;
            /*=====OVERLAY===== */
            //Fix the body
            body.classList.add('fixed');

            const overlay = createDiv('overlay');
            body.appendChild(overlay);

            const detailsDiv = createDiv('details-movie');
            overlay.appendChild(detailsDiv);

            //Clone and append image
            const imageClone = image.cloneNode(true);
            detailsDiv.appendChild(imageClone);


            /*=====BUTTONS===== */
            const buttonsContainer = createDiv('buttons-container');
            detailsDiv.appendChild(buttonsContainer)
        
            let playButtonDiv = playButton();
            buttonsContainer.appendChild(playButtonDiv);

            // Like button with toggle functionality
            let likeButtonDiv = likeButton();
            buttonsContainer.appendChild(likeButtonDiv);

            //Close botton
            let xButtonDiv = closeButton();
            detailsDiv.appendChild(xButtonDiv);
            setupOverlay('.close-button');

            /*=====MOVIE INFO (Left side)===== */
            const detailsMetadata = createDiv('metadata-container');
            const metadataInfo = createDiv('metadata-info');

            movieInfo = movieData('80', '2020', '2hr 45min', 'R');

            metadataInfo.appendChild(movieInfo);

            const synopsisText = 'Esta historia épica de monstruos, magia y destino de gran éxito mundial es «sumamente entretenida y logra cautivarte», según Paste.';
            const synopsisElement = createSynopsis('metadata-synopsis', synopsisText);
            
            metadataInfo.appendChild(synopsisElement);
            detailsMetadata.appendChild(metadataInfo);
            
            /*=====MOVIE INFO (Right side)=====*/

            const cast = ['Henry Cavill', 'Anya Chalotra', 'Freya Allan'];
            const genre = ['Drama series', 'Series based on books', 'Series based on books'];
            const category = ['Imaginative', 'Exciting'];

            const metadataElement = createRightSide(cast, genre, category);

            detailsMetadata.appendChild(metadataElement);
            detailsDiv.appendChild(detailsMetadata)

            setupOverlay('.overlay', '.details-movie');
        })
    })
}

function movieData( percentData, yearData, durationData, maturityData){

    const metadataTime = document.createElement('DIV');
    metadataTime.classList.add('metadata-info-time');

    const percent = document.createElement('SPAN')
    percent.classList.add('metadata-percent')
    percent.textContent = `${percentData}% for you`;
    metadataTime.appendChild(percent);

    const year = document.createElement('SPAN')
    year.classList.add('metadata-year')
    year.textContent = `${yearData}`;
    metadataTime.appendChild(year);

    const duration = document.createElement('SPAN')
    duration.classList.add('metadata-duration');
    duration.textContent = `${durationData}`;
    metadataTime.appendChild(duration);

    const maturityRating = document.createElement('SPAN')
    maturityRating.classList.add('metadata-maturity');
    maturityRating.textContent = `${maturityData}`;
    metadataTime.appendChild(maturityRating);

    return metadataTime;
}

//Start MovieData functions
function toggleAnimation(element, activeClass, animationIn, animationOut, contentOn, contentOff) {
    if (element.classList.contains(activeClass)) {
        element.classList.remove(animationIn);
        
        element.classList.add(animationOut);
        element.classList.remove(activeClass, animationOut);
        element.innerHTML = contentOff;
    } else {
        element.classList.remove(animationOut);
        element.classList.add(activeClass, 'animate__animated', animationIn);
        element.innerHTML = contentOn;
    }
}

function setupOverlay(overlaySelector, stopPropagationSelector = null) {
    
    const overlay = document.querySelector('.overlay');
    const a = document.querySelector(overlaySelector);
    
    a.addEventListener('click', () => {
        document.body.classList.remove('fixed');
        overlay.remove(); 
    });
    
    if (stopPropagationSelector != null) {   
        const propagationElement = document.querySelector(stopPropagationSelector);
        propagationElement.addEventListener('click', (e) => {
            e.stopPropagation();
        });
    }
}

function createSynopsis(className, textContent) {
    if (typeof className !== 'string' || className.trim() === '') {
        throw new Error('className must be a non-empty string');
    }
    if (typeof textContent !== 'string' || textContent.trim() === '') {
        throw new Error('textContent must be a non-empty string');
    }

    const synopsisContainer = document.createElement('div');
    synopsisContainer.classList.add('metadata-info-synopsis');

    const synopsisParagraph = document.createElement('p');
    synopsisParagraph.classList.add(className);
    synopsisParagraph.textContent = textContent;

    synopsisContainer.appendChild(synopsisParagraph);

    return synopsisContainer;
}

function createRightSide(castData, genreData, categoryData) {
    const metadataInfoRight = document.createElement('div');
    metadataInfoRight.classList.add('metadata-info-right');

    let castList = [...castData].join(', ');
    castList = castList + ", More";
    
    let genreList = [...genreData].join(', ');
    
    let categoryList = [...categoryData].join(', ');
    
    let cast = createMetadataSection('Cast', castList);
    const genre = createMetadataSection('Genre', genreList);
    const category = createMetadataSection('This title is', categoryList);

    metadataInfoRight.appendChild(cast);
    metadataInfoRight.appendChild(genre);
    metadataInfoRight.appendChild(category);

    return metadataInfoRight;
}

function createMetadataSection(label, content) {
    const section = document.createElement('div');
    section.classList.add('metadata-info-section');

    const sectionContent = document.createElement('p');
    sectionContent.classList.add('metadata-info-text');
    sectionContent.innerHTML = `<span class="tag-metadata">${label}: </span>${content}`;

    section.appendChild(sectionContent);
    return section;
}

function playButton() {
    const playButtonDiv = document.createElement('BUTTON');
    playButtonDiv.classList.add('play-button');

    const playIcon = document.createElement('DIV');
    playIcon.innerHTML = `<svg xmlns="http://www.w3.org/2000/svg" fill="none" width="24" height="24" viewBox="0 0 24 24" role="img" data-icon="PlayStandard" aria-hidden="true"><path d="M5 2.69127C5 1.93067 5.81547 1.44851 6.48192 1.81506L23.4069 11.1238C24.0977 11.5037 24.0977 12.4963 23.4069 12.8762L6.48192 22.1849C5.81546 22.5515 5 22.0693 5 21.3087V2.69127Z" fill="currentColor"></path></svg>`
    playIcon.classList.add('play-icon');
    playButtonDiv.appendChild(playIcon);

    const playText = document.createElement('SPAN')
    playText.textContent = 'Play'
    playText.classList.add('play-text');
    playButtonDiv.appendChild(playText);

    return playButtonDiv
}

